import * as actionType from './actionType'
import {getData} from 'util'
import {API_WAREHOUSES_MAINLOCATIONS_PAGE} from 'api'
//同步
export const update = (object) => ({
  type: actionType.PLACEQUERY_UPDATE,
  data: object
})

//异步

// 获取列表
export const handleGetList = (reqData) => (dispatch) => {
  dispatch({type: actionType.PLACEQUERY_UPDATE, data: {tableLoading: true}})
  let reqObj = reqData.searchParams
  for (let i in reqObj) {
    if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
      reqObj[i] = reqObj[i].value
    }
  }
  getData({api: API_WAREHOUSES_MAINLOCATIONS_PAGE, ...reqData}).then(data => {
    // 相应的逻辑判断
    let {page, size} = reqData.searchParams
    dispatch({
      type: actionType.PLACEQUERY_UPDATE,
      data: {
        page,
        size,
        tableLoading: false,
        total: data.result.total,
        searchResult: data.result.list || []
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.PLACEQUERY_FILE
    })
  })
}



